    <el-dialog
      title="正则校验"
      :visible.sync="dialogVisible"
      width="30%"
    >

      <el-form :model="regular" label-width="120px" size="small">
        <el-form-item label="正则表达式">
          <el-input v-model="regular.pattern" />
        </el-form-item>
        <el-form-item label="提示信息">
          <el-input v-model="regular.message" />
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" size="small" @click="saveRegular">确 定</el-button>
      </span>
    </el-dialog>
  </el-card>
</template>

<script>
// import MInput from './assembly/input'
// import MTextarea from './assembly/textarea'
export default {
  components: {
  },
  props: {
    // 中间是否拖动
    drag2: {
      type: String,
      default: () => {
        return '1'
      }
    },
    // 组件对象
    form: {
      type: Object,
      default: () => {
        return {
          // 展示字段
          showFrom: [],
          // 校验
          rules: []
        }
      }
    },
    formSetting: {
      type: Object,
      default() {
        return {}
      }
    },
    // 传递过来的可选字段
    fields: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      activeName: '1',
      formclass: '',
      // 垃圾桶
      lj: false,
      // 正则
      regular: {
        pattern: '/^[1][3,4,5,7,8][0-9]{9}$/',
        message: '请输入正确的手机号'
      },
      dialogVisible: false
    }
  },
  computed: {
    fieldsData() {
      let ret = []
      // 是数组
      if (Array.isArray(this.fields)) {
        ret = this.fields
      }
      return ret
    },
    zzcindex() {
      return this.drag2 === '1' ? -10 : 10
    }
  },
  watch: {
    form: function() {
      const thiz = this
      this.formclass = 'animated bounceInRight'
      const timer = setTimeout(function() {
        thiz.formclass = ''
        window.clearTimeout(timer)
      }, 1000)
      console.log('bianhua')
    }
  },
  created() {
  },
  methods: {
    drop() {
      this.lj = false
      this.$emit('deleteComponent')
    },
    dragover() {
      this.lj = true
      // console.log('滑动时')
    },
    dragleave() {
      this.lj = false
      // console.log('出去了')
    },
    // 删除选项
    handleDelete(index) {
      this.form.deleteOption(index)
    },
    addOption() {
      this.form.addoption()
    },
    saveRegular() {
      const { pattern, message } = this.regular
      if (!pattern || !message) {
        this.$message.error('请填写完正则表达式及未通过表达式时的提示信息!')
        return
      }
      let isReg
      try {
        // eslint-disable-next-line no-eval
        isReg = eval(pattern) instanceof RegExp
      } catch (e) {
        isReg = false
      }
      if (isReg === true) {
        const aa = pattern.substring(1, pattern.length - 1)
        const data = {
          'pattern': new RegExp(aa),
          'message': message
        }
        this.form.addRules(data)
        this.dialogVisible = false
      } else {
        this.$message.error('您的正则表达式不正确!')
      }
    }
  }
}
</script>

